
import { defineComponent, ref } from "vue";
import * as NProgress from "nprogress";
import { Slide } from "vue3-burger-menu";
import { useToast } from "vue-toastification";
import PluginsManager from "@/components/PluginsManager.vue";
import ImageUpload from "@/components/ImageUpload.vue";
import ImagePreview from "@/components/ImagePreview.vue";
import Config from "@/components/Config.vue";
import Help from "@/components/Help.vue";

interface Image {
  name: string
}

declare interface HomeData {
  pluginUrl: string;
  just: string | null;
  defaultConfig: [];
  defaultPlugins: string[];
  sourceImages: Image[];
  resultImages: Image[];
}

export default defineComponent({
  name: "Home",
  components: {
    Slide,
    PluginsManager,
    ImageUpload,
    ImagePreview,
    Config,
    Help,
  },
  data(): HomeData {
    return {
      pluginUrl: "",
      just: null,
      defaultConfig: [],
      defaultPlugins: [],
      sourceImages: [],
      resultImages: [],
    };
  },
  setup: () => {
    const imageUpload = ref(ImageUpload);
    const pluginsManager = ref(PluginsManager);
    return {
      imageUpload,
      pluginsManager,
    };
  },
  created() {
    if (this.$route.query["config"]) {
      this.defaultConfig = JSON.parse(
        // eslint-disable-next-line no-undef
        Buffer.from(this.$route.query["config"].toString(), "base64").toString(
          "binary"
        )
      );
      console.log(this.defaultConfig);
    }
    if (this.$route.query.plugin) {
      this.defaultPlugins = [this.$route.query.plugin.toString()];
    }
    if (this.$route.query.just) {
      this.just = this.$route.query.just.toString();
    }
  },
  methods: {
    loadPlugin(pluginUrl: string) {
      console.log("loaded plugin: ", pluginUrl);
      this.pluginsManager.importPlugin(pluginUrl, pluginUrl);
    },
    onMorePlugins() {
      window.location.replace("/marketplace");
    },
    changeImages(files: any) {
      this.sourceImages.push(files.file);
    },
    async processImages() {
      const toast = useToast();
      const imageCount = this.sourceImages.length;
      let convertedCount = 0;
      toast.info(`converting ${imageCount} images started`);
      this.resultImages = [];
      let tasks = [];
      for (let sourceImage of this.sourceImages) {
        tasks.push(
          this.process(sourceImage).then((image) => {
            let file = new File([image], sourceImage.name, {
              type: "image/png",
            });
            this.resultImages.push(file);
            convertedCount++;
            toast.success(
              `converted ${convertedCount} of ${imageCount} images`
            );
          })
        );
      }
      this.imageUpload.images = [];
      this.sourceImages = [];
      return Promise.all(tasks).then(() => {
        toast.info(`converted ${convertedCount} images`);
      });
    },
    async process(imageFile: any) {
      NProgress.configure({ parent: "#process" });
      NProgress.start();
      let img = new Uint8Array(await imageFile.arrayBuffer());
      for (let plugin of this.pluginsManager.plugins) {
        NProgress.inc(0.2);
        if (plugin.enabled) {
          img = await plugin.plugin.process(plugin.config, img);
        }
      }

      NProgress.done();
      NProgress.remove();
      return img;
    },
  },
});
